
body {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
}
.fade-enter-active, .fade-leave-active {
  transition-property: opacity;
  transition-duration: .25s;
}
.fade-enter-active {
  transition-delay: .25s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.nav {
  height: 5rem !important;
  background-color: #7832A1 !important;
}
.nav-item {
  color: #fff !important;
  font-weight: 500 !important;
}
.nav-toggle {
  height: 5rem !important;
}
.nav-toggle:hover {
  background-color: #5b267a !important;
}
.nav-toggle span {
  background-color: #fff !important;
  width: 1rem !important;
  height: 2px !important;
}
.nav-right.nav-menu.is-active .nav-item {
  color: #666 !important;
}
.nav-item a:not(.button).is-tab.is-active, a.nav-item:not(.button).is-tab.is-active {
  border-bottom-color: #fff !important;
}
.nav-left .nav-item {
  width: 5rem;
}
.nav-left .nav-item img {
  width: 100%;
  /* min-width: 100%; */
  max-height: 100%;
}



